<template>
  <div class="app">
    <v-header :seller="seller"></v-header>
    <div class="tab">
        <div class="tab-item">
          <router-link to="/goods"></router-link>
        </div>
        <div class="tab-item">
          <router-link to="/ratings"></router-link>
        </div>
        <div class="tab-item">
          <router-link to="/seller"></router-link>
        </div>
    </div>
    <keep-alive>
        <router-view :seller="seller"></router-view>
    </keep-alive>
  </div>
</template>

<script type="text/ecmascript-6">
  import {datajson} from './common/js/datajson'
  import {urlParse} from './common/js/util';
  import header from './components/header/header';
  const ERR_OK = 0;
  export default {
    name: 'app',
    components:{
      'v-header':header
    },
    data(){
      return {
        seller:{
          id: (() => {
            let queryParam = urlParse();
            return queryParam.id;
          })()
        }
      }
    },
    created(){
      /*this.$http.get('/api/seller?id=' + this.seller.id).then((response) => {
        response = response.body;
        if (response.errno === ERR_OK) {
          this.seller = Object.assign({}, this.seller, response.data);
        }
      });*/

      //
      if(datajson.errno === ERR_OK){
        this.seller = Object.assign({},this.seller,datajson.seller)
        //console.log(this.seller)
      }


    },

  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import './common/stylus/mixin'
  .app
    .tab
        display:flex
        width:100%
        height:40px
        line-height:40px
        border-bottom-1px(rgba(7,17,27,0.1))
        .tab-item
          flex:1
          text-align:center
          & > a
            display:block
            font-size :14px;
            color:rgb(77,85,93)
            &.active
              color:rgb(240,20,20)
</style>



